import React from 'react';
import Box from '@mui/material/Box';
import { NavButton, DropDown, StyledMenuList} from './NavbarStyles';
import MenuItem from '@mui/material/MenuItem';

function Tabs() {

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleOpenMenu = (event) => {
        console.log(event.currentTarget);
        setAnchorEl(event.currentTarget);
    };

    const handleCloseMenu = () => {
        setAnchorEl(null);
    };

    return (
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-evenly', flexGrow: 1 }}>
            <NavButton onClick={() => window.location.href = '/DermPal/'}>Home</NavButton>
            <Box>
                <NavButton onClick={handleOpenMenu}>Features</NavButton>
                <DropDown anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleCloseMenu} MenuListProps={{ "aria-labelledby": "basic-button", component: StyledMenuList}} >
                    <MenuItem onClick={() => window.location.href = '/DermPal/dermai'} >DermAI</MenuItem>
                    <MenuItem onClick={() => window.location.href = '/DermPal/dermjournal'} >DermJournal</MenuItem>
                    <MenuItem onClick={() => window.location.href = '/DermPal/dermmart'} >DermMart</MenuItem>
                </DropDown>
            </Box>
            <NavButton onClick={() => window.location.href = '/DermPal/about'}>About</NavButton>
            <NavButton onClick = {() => window.location.href = '/DermPal//our-team'} >Our Team</NavButton>
        </Box>
    );
}

export default Tabs;